<script>
export default {
  data() {
    return {
      user: {},
      loading: false,
      message: "",
      step: 1,
    };
  },
  mounted() {
    this.$store.dispatch("dictionary/getCountries");
    this.$store.dispatch("dictionary/getIndustries");
    if (this.loggedIn) {
      this.$router.push("/");
    }
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  methods: {
    handleSignup(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {

          if (this.user.password != this.user.confirm_password) {
            this.$notify({
              title: "Error",
              message: "Password confirmation does not match",
              type: "error",
            });
            return;
          }

          this.loading = true;
          let data = this.user;
          data.username = data.email;
          data.role_id = 1;
          data.login_attempts = 5;
          this.$endpoint.signup(data).then(
            (res) => {
              this.$router.push("/login");
              this.$notify({
                title: "Success",
                message: res.data.message,
                type: "success",
              });
            },
            (error) => {
              this.loading = false;
              this.message =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
              this.$notify({
                title: "Error",
                message: this.message,
                type: "error",
              });
            }
          );
        } else {
          return false;
        }
      });
    },
  },
};
</script>